import React, { Component } from 'react';
import TokenBoxItem from './TokenBoxItem';
import { Message } from 'semantic-ui-react';


class TokenBox extends Component {

    constructor(props) {
        super(props);    
        
        this.handleDrop = this.handleDrop.bind(this);
    }

    handleDrop(token, pos) {
        this.props.handleDrop(token, pos);
    }

    renderItems = (tokens) => {
            const toRender = <div>{tokens.map(token => {
                if(token.position == 'token-box') {
                return (
                <TokenBoxItem key={token.address} token={{...token}} handleDrop={(token, pos) => this.handleDrop(token, pos)}></TokenBoxItem>);
                } else {return ''};            
            })}</div>;
            
            return toRender;
        
      }

    render() {
        if (this.props.signedIn)
        return (
            <div className='tokens-box'>            
                {this.renderItems(this.props.tokens)}   
            </div>
        );

        return (
            <Message
            warning
            header='Sign in with Metamask'
            content='Please sign in with Metamask in order to see your balances and place bets.'
          />
        );
    }
}


export default TokenBox;